import React, { useEffect, useState, useCallback } from "react";
import MUIDataTable from "mui-datatables";
import Grid from '@mui/material/Grid';
import { Avatar } from "@mui/material";
import { getByPage } from '../Services';


const columns = [
    {
        name: "id",
        label: "id",
        options: {
            filter: false,
            sort: true,
        }
    },
    {
        name: "name",
        label: "Name",
        options: {
            filter: true,
            sort: true,
        }
    },
    {
        name: "image",
        label: "Image",
        options: {
            filter: false,
            sort: false,
            customBodyRender: (value) => <Avatar src={value} alt="image" sx={{ width: 66, height: 66 }} />

        }
    },
    {
        name: "status",
        label: "status",
        options: {
            filter: true,
            sort: true,
        }
    },
    {
        name: "species",
        label: "species",
        options: {
            filter: true,
            sort: true,
        }
    },
    {
        name: "gender",
        label: "gender",
        options: {
            filter: false,
            sort: true,
        }
    },
];


const Characters = (props) => {

    const [name, setName] = useState("");
    const [status, setStatus] = useState("");
    const [species, setSpecies] = useState("");
   
    const { count, setrmcharacter } = props;
    const handleFilter = useCallback(async () => {
        const fetchApi = await fetch(`https://rickandmortyapi.com/api/character/?name=${name}&status=${status}&species=${species}`);
        const respoData = await fetchApi.json();
        setrmcharacter([...respoData.results]);

    }, [name, species, status, setrmcharacter]);


    const options = {
        serverSide: true,
        rowsPerPage: 20,
        confirmfilter: false,
        filter: true,
        rowsPerPageOptions: [20],
        count: count,
        onTableChange: (action, tablestate) => {
            if (action === 'changePage') {
                const nextPage = tablestate.page + 1;
                getByPage({ setrmcharacter, page: nextPage })
            }
        },
        onFilterChange: (changedColumn, filterList) => {

            setName(filterList[1]);
            setStatus(filterList[3]);
            setSpecies(filterList[4]);
        },
        onSearchChange: (searchText) => {
            // console.log("search: " + searchText);
            setName(searchText);
            setStatus(searchText);
            setSpecies(searchText);
        },
    };

    useEffect(() => {
        handleFilter();
    }, [handleFilter]);
    return (

        <Grid container spacing={2} justifyContent='center' alignContent='center' alignItems='center'>
            <Grid item xs={12} >
                <MUIDataTable
                    title={"Character List"}
                    data={props.rmcharacter}
                    columns={columns}
                    options={options}

                />
            </Grid>
        </Grid>

    );

};
export default Characters;