import * as React from 'react';
import Pagination from '@mui/material/Pagination';
import { getByPage } from '../Services';
import Grid from '@mui/material/Grid';
import { Stack } from '@mui/material';

export default function PaginationRM(props) {
    const {setDatalist,setrmcharacter}=props;
    const pageHandler=(event,page)=>{
        
        getByPage({ setDatalist,page,setrmcharacter})
    }

  return (
    <Grid container  justifyContent='center'>
    <Grid item xs={12} sm={12}>
      <Stack>
      <Pagination count={40} onChange={pageHandler} style={{paddingBottom:20}}/>
      </Stack>
   
    </Grid>
        </Grid>

  );
}