export const getMortyByNameFilter= async (props)=>{
    const {setrmcharacter,filtertxt}= props

    try{
        const rickmyData=await fetch (`https://rickandmortyapi.com/api/character/?name=${filtertxt}`);
        const data=await rickmyData.json();

        setrmcharacter(data.results);
       console.log('data is ',data.results);
    
    }catch (error){
        console.log(error);
    }
};

