export const getByPage= async (props)=>{
    const {setrmcharacter,page}= props;

    try{
        const movieData=await fetch (`https://rickandmortyapi.com/api/character/?page=${page} `);
        const data=await movieData.json();

       //setDatalist(data);
       setrmcharacter(data.results);
      
    }catch (error){
        console.log(error);
    }
};

