import React, { useState, useEffect } from 'react';
import './App.css';
import Characters from './components/Characters';
import SearchBar from './components/SearchBar';
// import FilterCharacter from './components/FilterCharacter';
import Footer from './components/Footer';
import PaginationRM from './components/Pagination';

import RefreshSharpIcon from '@mui/icons-material/RefreshSharp';
import ExitToAppIcon from '@mui/icons-material/ExitToApp';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import MenuIcon from '@mui/icons-material/Menu';
import { Button, ThemeProvider } from '@mui/material';
import { createTheme } from '@mui/material';
import { useNavigate } from "react-router-dom";
import LightModeIcon from '@mui/icons-material/LightMode';



function App() {
  const [themeget, setThemeGet] = useState('dark')
  const [themeValue, setThemeValue] = useState(true)
  const theme = createTheme({
    palette: {
      mode: themeget,
    },
  });

  const [rmcharacter, setrmcharacter] = useState([{
    id: '',
    name: '',
    status: '',
    species: '',
    image: ''
  }]);
  const [count,setCount]=useState();

  useEffect(() => {
    async function getposts() {
      const fetchResponse = await fetch("https://rickandmortyapi.com/api/character");
      const respData = await fetchResponse.json();
      setrmcharacter(respData.results);
      setCount(respData.info.count)
    };
    getposts();

  }, []);

  const getPost = async (id) => {

    if (id == null) {
      console.log("error")
    }
    try {
      let respo = await fetch(`https://rickandmortyapi.com/api/character/${id}`, {
        method: 'GET'
      });
      console.log('id is ', id);
      const resPost = await respo.json();
      console.log(resPost);
      setrmcharacter([resPost]);
    } catch (error) {
      console.log(error);

    }
  }

  const refreshHandler = () => window.location.reload(true)

  const navigate = useNavigate();
  const logouthHandler = () => {
    navigate(`/`);
  }
  const modehHandler = () => {
    setThemeValue(!themeValue)
    if (themeValue === true) {
      setThemeGet('dark');
    }
    else {
      setThemeGet('light');
    }

  }


  return (

    <div className="App">
      <ThemeProvider theme={theme}>
        <Box sx={{ flexGrow: 1 }}>
          <AppBar position="static">
            <Toolbar>
              <IconButton
                size="large"
                edge="start"
                color="inherit"
                aria-label="open drawer"
                sx={{ mr: 2 }}
              >
               <MenuIcon/>
              </IconButton>
              <Typography
                variant="h6"
                noWrap
                component="div"
                sx={{ flexGrow: 1, display: { xs: 'none', sm: 'block' } }}
              >
                RICK AND MORTY APP
              </Typography>
              <Button variant="contained" onClick={modehHandler}><LightModeIcon />Switch Mode</Button>
              <Button variant="contained" onClick={refreshHandler}><RefreshSharpIcon />Refresh</Button>

              <SearchBar getPost={getPost} />
              <Button variant="contained" onClick={logouthHandler}><ExitToAppIcon />LogOut</Button>
            </Toolbar>
          </AppBar>
        </Box>
        {/* <FilterCharacter  setrmcharacter={setrmcharacter} /> */}
        <Characters  rmcharacter={rmcharacter} count={count} setrmcharacter={setrmcharacter} />
        <PaginationRM  setrmcharacter={setrmcharacter} />
        <Footer />
      </ThemeProvider>
    </div>

  );
}

export default App;
